<template>
    <svg :width="getWidth" :height="getHeight"  @mousemove="mouseMoving" @click="selected">
        <linearGradient :id="grad" x1="0" x2="100%" y1="0" y2="0">
            <stop :offset="getFill" :stop-color="activeColor" />
            <stop :offset="getFill" :stop-color="inactiveColor" />
        </linearGradient>
        <path :d="pointsToString" :fill="getGradId" :stroke="borderColor" :stroke-width="borderWidth" />

        <path :d="pointsToString" :fill="getGradId" />

    </svg>
</template>

<script type="text/javascript">
import Vue from 'vue';
import RatingItem from '../mixins/RatingItem.js';
export default Vue.extend({
    mixins: [RatingItem],
    methods: {
        calculatePoints() {
            // Override calculatePoints method in RatingItem.js
                this.points = this.points.map(points => {
                    points = this.normalisePathPoints(points);
                    return this.adjustPath(points)
                });
        },
        normalisePathPoints(points) {
            // Adds any spaces between points that letter and number
            var regex = /([a-z])([0-9])/ig;
            return points.replace(regex, (match, p1, p2) => {
                return p1 + " " + p2;
            });
        },
        adjustPath(points) {
            points = points.split(" ");
            points = points.map(point => {
                let pathPoint = ((this.size / this.originalWidth) * point) + (this.borderWidth / 2);
                return (Number.isNaN(pathPoint)) ? point : pathPoint;
            });
            return points.join(" ");
        }
    }
});
</script>
